@import '~scss/variables';

$our-members-photo-size: 200px;
$our-members-gap: 5px;
$our-members-gap-desktop: 10px;
$our-members-breakpoint: $screen-sm;

.our-members-container {
  display: flex;
  flex-wrap: wrap;
  margin: -$our-members-gap;
  @media (min-width: $our-members-breakpoint) {
    margin: -$our-members-gap-desktop;
  }
}

.our-members-toolbar {
  max-width: 800px;
  margin-left: auto;
}

.our-member {
  flex: 0 0 calc(50% - #{$our-members-gap * 2});
  margin: $our-members-gap;
  cursor: pointer;

  @media (min-width: 500px) {
    flex: 0 0 calc(33.33% - #{$our-members-gap * 2});
  }

  @media (min-width: 960px) {
    margin: $our-members-gap-desktop;
    flex: 0 0 calc(25% - #{$our-members-gap-desktop * 2});
  }

  @media (min-width: 1200px) {
    margin: $our-members-gap-desktop;
    flex: 0 0 calc(20% - #{$our-members-gap-desktop * 2});
  }

  ff-img {
    position: relative;
    display: block;
    width: 100%;
    padding-bottom: 100%;
    background-color: fade_out($extra-bg-color, .7);

    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }

  h5 {
    margin: 10px 0 5px 0;
    font-size: 16px;
    font-weight: bold;
  }

  h6 {
    margin: 0;
    font-size: 12px;
    font-weight: normal;
    text-transform: uppercase;
  }
}

.our-member-modal {
  &, .dw-modal-scroll {
    width: 100%;
  }
}

.our-member-header {
  ff-img {
    display: block;
    flex: 0 0 $our-members-photo-size;
    max-width: $our-members-photo-size;
    height: $our-members-photo-size;
    margin: 0 15px 15px 0;

    @supports (object-fit: cover) {
      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }
  }

  @media (min-width: 560px) {
    display: flex;

    ff-img {
      margin-bottom: 0;
    }
  }
}

.our-member-details {
  &, .ff-row > & {
    display: block;
  }

  h4 {
    margin: 5px 0;
    font-size: 24px;
    font-weight: bold;
  }

  h5 {
    margin: 8px 0;
    font-size: 13px;
    font-weight: normal;
    text-transform: uppercase;
    opacity: 0.8;
  }

  h6 {
    margin: 8px 0;
    font-size: 18px;
  }
}

.our-member-contacts { // emails + phones
  margin: 20px 0;
}

.our-member-emails, .our-member-phones {
  margin: 4px 0;

  & > span {
    display: inline-block;
    margin-right: 8px;
    white-space: nowrap;

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.our-member-urls {
  margin-top: 20px;
  font-size: 0;

  & > span {
    margin-right: 5px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  .fa {
    width: 28px;
    text-align: center;
    font-size: 28px;
  }
}

.our-member-field {
  margin-top: 15px;

  & > span:first-of-type {
    text-transform: uppercase;
    font-size: .85em;
    opacity: .65;
  }
}

.our-members-spin {
  width: 100%;
  height: 60px;
}

our-members-embedding {
  display: block;
}
