@import '~scss/variables';

up-photo ff-panel {
  height: 100%;
}

up-photo .ff-panel-body {
  display: flex;

  ff-img {
    position: absolute;
    top: 0;
    height: 100%;
    max-width: 100%;

    img {
      display: block !important;
      height: 100%;
      max-height: 100%;
      width: auto;
      max-width: none;
    }

    &[img-placeholder].ff-img-placeholder::before {
      position: absolute;
    }
  }

  .ff-row {
    position: relative;
    width: 100%;
    min-height: 130px;
  }

  ff-btn-group {
    position: absolute;
    bottom: 0;
    right: 0;

    @media (max-width: 340px), (min-width: 640px) and (max-width: 960px), (min-width: $sidebar-breakpoint) and (max-width: 1180px) {
      bottom: calc(100% + 18px);
    }
  }
}
