@import '~scss/variables';

.user-search membership-filter {
  margin-bottom: .5rem;
}

.us-show-row {
  margin-top: -12px;

  ff-info {
    flex: 0 0 30px;
    justify-content: center;
    padding: 0;
    font-size: 10px;
  }
}

.us-results {
  ff-static {
    word-break: break-all;
  }

  ff-static, .ff-static {
    overflow: visible;
  }

  .ff-hint {
    word-break: normal;
  }
}
